/**
 * Billservice model events
 */

'use strict';

import {EventEmitter} from 'events';
import Billservice from './billservice.model';
var BillserviceEvents = new EventEmitter();

// Set max event listeners (0 == unlimited)
BillserviceEvents.setMaxListeners(0);

// Model events
var events = {
  'save': 'save',
  'remove': 'remove'
};

// Register the event emitter to the model events
for (var e in events) {
  var event = events[e];
  Billservice.schema.post(e, emitEvent(event));
}

function emitEvent(event) {
  return function(doc) {
    BillserviceEvents.emit(event + ':' + doc._id, doc);
    BillserviceEvents.emit(event, doc);
  }
}

export default BillserviceEvents;
